import React, {Component} from 'react';
import {Col, Row} from "reactstrap";

class FlexibleCardHeader extends Component {

  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    return (
      <Row style={this.props.headerStyle} id='headerContainer'>
        <Col xs={8} sm={8} md={8} lg={8} xl={8}>
          <label style={{margin: '0 0 0 0', padding: '3px 0 3px 0', fontWeight: '600', opacity: '0.8'}}>{this.props.title ? this.props.title : ''}</label>
        </Col>
        <Col xs={4} sm={4} md={4} lg={4} xl={4} id='cardButtons'>{this.props.buttons}</Col>
      </Row>

    )
  }
}

export default FlexibleCardHeader;