import React, {Component} from 'react';
import {Col, Form, FormGroup, Input, Label, Modal, ModalBody, ModalFooter, ModalHeader, UncontrolledTooltip} from 'reactstrap';
import ReactLoading from "react-loading";
import Select from "../local_node_modules/react-select";
import Joyride from "react-joyride";


/**
 * Creates or Updates a given Modal.
 */
class SaveEntityModal extends Component {


  constructor(props) {
    super(props);
    this.state = {
      error: {},
      mode: undefined,
      saving: false,
      item: undefined,
      steps: [
        {
          target: 'form div:nth-child(1) div',
          content: 'Yeni oluturacanz onay ak iin belge tr seiniz.',
          placement: "auto",
          disableBeacon: 'true'
        },
        {
          target: '#approvalContainer div .row:nth-child(1)',
          content: 'Belgeyi onaylayacak kiiyi seiniz.',
          placement: "auto",
          disableBeacon: 'true'
        },
        {
          target: '#approvalContainer div .row:nth-child(2)',
          content: 'Belgenin onaylanma eklini seiniz.',
          placement: "auto",
          disableBeacon: 'true'
        },
        {
          target: '#approvalContainer div .row:nth-child(3) .row:nth-child(1) ',
          content: 'Gnderim skln seiniz.',
          placement: "auto",
          disableBeacon: 'true'
        },
        {
          target: '#approvalContainer .maxrepeat',
          content: 'Gnderim ileminin ka defa tekrar edileceini seiniz.',
          placement: "auto",
          disableBeacon: 'true'
        },
        {
          target: '#approvalContainer div .row:nth-child(3) .row:nth-child(3) ',
          content: 'Her gnderim yapldnda bilgilendirilecek olan kiileri seiniz.',
          placement: "auto",
          disableBeacon: 'true'
        },
        {
          target: '#approvalContainer div .row:nth-child(3) .row:nth-child(4) ',
          content: 'Zaman am durumunda bilgilendirilecek olan kiileri seiniz.',
          placement: "auto",
          disableBeacon: 'true'
        },
        {
          target: '#approvalContainer div:nth-child(3) > div:nth-child(3)',
          content: 'Belge onaylandnda bilgilendirilecek olan kiileri seiniz.',
          placement: "auto",
          disableBeacon: 'true'
        },
        {
          target: '#approvalContainer div:nth-child(3) > div:nth-child(4)',
          content: 'Belge reddedildiinde bilgilendirilecek olan kiileri seiniz.',
          placement: "auto",
          disableBeacon: 'true'
        },
        {
          target: '#approvalContainer button',
          content: '"+" butonuna tklayarak onay aka basamak ekleyebilirsiniz.',
          placement: "auto",
          disableBeacon: 'true'
        },
      ],
      locale: {
        back: 'Geri',
        close: 'Kapat',
        last: 'Son',
        next: 'leri',
        skip: 'Atla',
      },
      run:false
    };

    this.toggle = this.toggle.bind(this);
    this.onSave = this.onSave.bind(this);
    this.onChange = this.onChange.bind(this);
    this.validateClass = this.validateClass.bind(this);
    this.validateField = this.validateField.bind(this);
    this.validateMessage = this.validateMessage.bind(this);
  }

  componentDidMount() {
    let mode = '';
    let item = this.props.item;
    if (item) {
      mode = item.id ? 'edit' : 'create';
    }

    this.setState({mode: mode, item: item})
  }

  toggle() {
    this.props.toggle();
  }

  onSave() {
    this.setState({saving: true});
    this.props.onSave(this.state.item, this.state.mode);
    this.setState({saving: false});
  }

  validateField(fieldName, value, must) {

    let error = this.state.error;
    let item = this.state.item;

    const noError = {valid: true, message: ''};

    if (value === undefined || item === '' || (must && (value === null || value === ''))) {
      error[fieldName] = {valid: false, message: "Bu alan zorunludur."}
    }
    else {
      error[fieldName] = Object.assign({}, noError);
      item[fieldName] = value;
      this.setState({item: item});
    }

    this.setState({error});
    return error[fieldName];
  }

  validateClass(id) {

    let error = this.state.error;
    return (error[id] === undefined ? "" : (error[id].valid ? " has-success" : " has-danger"));
  }

  validateMessage(id) {
    let error = this.state.error;
    return (error[id] === undefined ? '' : (error[id].message));
  }

  createRequestParams() {
    let url = this.props.url;
    let params = {
      url: url,
      method: 'post'
    };

    return params;
  }

  onChange(event, field) {
    if (!event) return;

    const target = event.target;
    const value = field.type === "checkbox" ? target.checked : target.value;
    const name = target.id;
    this.setState(() => this.validateField(name, value, field.must));
  }

  onChangeSelect(e, name) {

    if (!e) return;

    const value = e.value;

    let item = this.state.item;
    item[name] = value;
    this.setState({item: item}, () => this.validateField(name));

  }

  activateJoyride = () => {
    this.setState({run : !this.state.run});
  }

  callback = (data) => {
    if (data.action === 'close' || data.action === 'skip' || data.type === 'tour:end') {
      this.setState({run:false})
    }
  }


  render() {
    return (
      <Modal isOpen={this.props.isOpen} size="lg">
        <Joyride
            run={this.state.run}
            steps={this.state.steps}
            locale={this.state.locale}
            callback={this.callback}
            continuous={true}
            showProgress={true}
            showSkipButton={true}
            disableScrollParentFix={true}
            disableScrolling={true}
            styles={{
              options: {
                zIndex: 999999999999999,
                primaryColor: "#50a8e2",   // "#fc9636"
              }
            }}
        />
        <ModalHeader toggle={this.toggle}>{this.state.mode === 'create' ? 'Yeni Olutur' : 'Dzenle'}
          <button className="helpButton" onClick={() => {
            this.activateJoyride()
          }} style={{position: "absolute", right: "70px", alignSelf: "center", marginTop: "-8px"}}>
            <span className="tooltipText">Size yardmc olabilirim</span>
            <i className="far fa-question-circle helpButtonIcon" style={{opacity: ".5"}}/></button>
        </ModalHeader>
        <ModalBody>
          {this.state.saving &&
          <ReactLoading type="spinningBubbles" style={{width: "70px", height: "70px", margin: "20px auto 0 auto", fill: "#888"}}/>
          }
          {this.props.fields && this.state.item && this.renderFields()}
        </ModalBody>

        <ModalFooter>
          <button type="button" onClick={this.toggle}
                  disabled={this.state.saving}
                  style={{
                    background: ' linear-gradient(rgba(125, 125, 125, 0.57), rgb(111, 111, 111))',
                    padding: '8px 45px 8px 12px'
                  }}
                  className="TekBtn vazgec">Kapat
          </button>
          <button type="button" style={{padding: '8px 45px 8px 12px'}} onClick={this.onSave}
                  disabled={this.state.saving} className=" TekBtn kaydet pull-right">Kaydet
          </button>
        </ModalFooter>

      </Modal>
    )
  }

  renderFields() {
    let fields = this.props.fields;
    let self = this;

    function renderField(field) {
      if (field.onlyEditMode && self.state.mode !== 'edit') {
        return "";
      }

      function renderInput(field) {
        if (field.Cell && field.Cell !== null) {
          return field.Cell();
        }

        if (field.type === 'select') {
          return <Select name={field.key}
                         disabled={self.state.mode === 'edit'}
                         value={self.props.item[field.key]}
                         clearable={false}
                         placeholder="Seiniz"
                         onChange={e => self.onChangeSelect(e, field.key)}
                         options={self.state.mode === 'edit' ? field.selectAllData : field.selectData}/>
        } else if (field.type === 'textarea') {
          return <textarea id={field.key} style={field.style !== undefined ? field.style : {"width": "100%"}} value={self.state.item[field.key]}
                           onChange={e => self.onChange(e, field)}/>
        } else if (field.type === 'checkbox') {
          return <Input type={field.type} id={field.key} style={field.style && field.style} checked={self.state.item[field.key]}
                        onChange={e => self.onChange(e, field)}/>
        }
        return <Input type={field.type} id={field.key} style={field.style !== undefined ? field.style : {"width": "100%"}}
                      value={self.state.item[field.key]} onChange={e => self.onChange(e, field)}/>
      }

      return <div key={field.key} className="col-sm-6">
        <FormGroup row className={self.validateClass(field.key)}>
          <Label sm='6'>{field.label}{field.must ? "*" : ""}</Label>
          <Col sm={6}>
            {renderInput(field)}
            {field.type && field.type !== 'select' && self.validateMessage(field.key) !== "" &&
            <UncontrolledTooltip placement="right" target={field.key} delay={0}>
              {self.validateMessage(field.key)}
            </UncontrolledTooltip>
            }
          </Col>
        </FormGroup>
      </div>;
    }

    return (
      <Form>
        <div className="col-sm-12 row">
          {
            fields.map(renderField)
          }
        </div>
        {this.props.note && !(this.props.note.onlyEditMode && this.state.mode !== 'edit') && this.props.note.Cell && this.props.note.Cell()}
      </Form>
    );
  }

}

export default SaveEntityModal;



