    getOptions = (input) => {
        let self = this;
        if (input && input.length > 2) {
            let params = {
                url: `/employee/searchEmployee?search=${input}&customerId=${self.props.currentUser.customerId}`,
                method: "get",
            };
            return requestK(params)
                .then((response) => {
                    return response.data;
                }).then((data) => {
                    return {
                        options: data.map(item => ({
                            value: item.employeeUserId,
                            label: item.name + " " + item.surname + " [Tckn: " + item.tckn + "]",
                            data: item
                        }))
                    };
                });
        } else {
            return Promise.resolve({options: []});
        }
    }
    getGrants = (input) => {
        let self = this;
        if (input && input.length > 2) {
            let params = {
                url: `/role/searchGrant?search=${input}`,
                method: "get",
            };
            return requestK(params)
                .then((response) => {
                    return response.data;
                }).then((data) => {
                    return {
                        options: data.map(item => ({
                            value: item.id,
                            label: item.description.tr,
                            data: item
                        }))
                    };
                });
        } else {
            return Promise.resolve({options: []});
        }
    }

