import React, {Component} from 'react';
import FlexibleCardHeader from "./FlexibleCardHeader";
import FlexibleCardBody from "./FlexibleCardBody";

class FlexibleCard extends Component {

	constructor(props) {
		super(props);

		this.state = {};
	}

	render() {
		return (
			<div className='fl-card'>
				{this.props.title &&
				<FlexibleCardHeader title={this.props.title} headerStyle={this.props.headerStyle} buttons={this.props.buttons}/>
				}

				<FlexibleCardBody content={this.props.children} bodyStyle={this.props.bodyStyle}/>
			</div>
		)
	}
}

export default FlexibleCard;